/* emoji */
/* .emoji{display: inline-block; width: 28px; height: 28px; font-size: 0;  vertical-align: middle; background: url('/static/img/qq_exp.png') 0 0 no-repeat; background-size: 434px 202px;  border-right: 1px solid #ccc; border-bottom: 1px solid #ccc;} */
.emoji-panel{height: 200px; overflow: hidden;}
.emoji-leave-active, .emoji-enter-active{
  -webkit-transition: height .4s ease;
  transition: height .4s ease;
}
.emoji-leave-active, .emoji-enter{
  height: 0px !important;
}
.emoji-leave, .emoji-enter-active{
  height: 200px;
}
.fade-leave-active, .fade-enter-active{
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
}
.fade-leave-active, .fade-enter{
  opacity: 0;
}
.fade-leave, .fade-enter-active{
  opacity: 1;
}
.slide-left-leave-active, .slide-left-enter-active{
  -webkit-transition: left 2.4s ease;
  transition: left 2.4s ease;
}
.slide-left-leave-active, .slide-left-enter{
  left: -100%;
}
.slide-left-leave, .slide-left-enter-active{
  left: 0;
}
.emoji-panel .ctx{position: relative; margin-left: -10px; margin-right: -10px;}
/* .emoji-panel .ctx ul{position: absolute;} */
.emoji-panel .ctx .slide-item{float: left;}
.emoji-panel .ctx .slide-item i{display: inline-block; width: 100%; height: 100%; background-size: contain;}
.emoji{display: inline-block; -webkit-box-sizing: border-box; box-sizing: border-box; font-size: 0; padding: 8px;}
